@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/robotomedium.woff2") format("woff2"), url("../fonts/robotomedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #F8FDFF;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #E5E5E5;
  -webkit-text-fill-color: #E5E5E5;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
}

.button {
  display: inline-flex;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-family: "Roboto Condensed";
  font-weight: 700;
}
.button--blue {
  background-color: #00B2FF;
  color: #F2F5F6;
}
.button--light {
  background-color: #F8FDFF;
  color: #00B2FF;
  border: 1px solid #00B2FF;
  border-radius: 3px;
}
.button--dark {
  background-color: #041827;
  color: #F2F5F6;
}
.button--darker {
  background-color: #041427;
  color: #FFFFFF;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.about {
  padding: 60px 0 100px;
}

.about__container {
  display: grid;
  grid-template-columns: 150px auto;
  column-gap: 100px;
}

.about__image img {
  clip-path: url("#about-clip");
}

.about__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about__info h3 {
  margin: 0;
  margin-bottom: 5px;
  font-size: 36px;
  line-height: 51px;
}
.about__info p {
  margin: 10px 0;
  font-family: "PT Mono";
  line-height: 22px;
}

.about__button {
  margin-top: 15px;
  width: 180px;
  padding: 13px;
  display: flex;
  justify-content: center;
  font-size: 14px;
  line-height: 16px;
}

.features {
  padding: 120px 0 60px;
}

.features__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  list-style: none;
}

.features__item {
  padding: 40px 45px 35px;
  list-style: none;
  box-shadow: inset 5px 0px 0px 0px #00B2FF, inset 0px 0px 0px 1px #EBEBEB;
  box-sizing: border-box;
  outline: none;
  transition: background-color 200ms;
}
.features__item:hover, .features__item:focus {
  background-color: #00B2FF;
  color: #F2F5F6;
  border: none;
  box-shadow: none;
}
.features__item:hover svg, .features__item:focus svg {
  fill: #F2F5F6;
}
.features__item:hover h3, .features__item:focus h3 {
  color: #F2F5F6;
}

.features__item svg {
  margin-bottom: 20px;
  fill: #00B2FF;
}

.features__item h3 {
  margin: 0;
  margin-bottom: 15px;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  color: #00B2FF;
}

.features__item p {
  margin: 0;
  font-family: "PT Mono";
}

.intro {
  min-height: 778px;
  background-color: #041827;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%), url("../../img/main-prod.jpg") center/cover no-repeat;
}

.intro__container {
  padding: 205px 0 280px;
}

.intro__info {
  max-width: 720px;
  margin: 0 auto;
}

.intro h2 {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #F2F5F6;
}

.intro p {
  margin: 0;
  margin-bottom: 40px;
  font-family: "PT Mono";
  font-size: 22px;
  line-height: 155%;
  color: #F2F5F6;
}

.intro__button {
  padding: 15px 25px;
  border-radius: 3px;
}

.logo {
  width: 215px;
  cursor: pointer;
}

.logo svg {
  fill: #00B2FF;
}
@media (min-width: 768px) {
  .logo svg {
    width: 55px;
    height: 55px;
  }
}

.logo__text {
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
@media (max-width: 320px) {
  .logo__text {
    display: none;
  }
}
.logo__text--big {
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: #F2F5F6;
}
.logo__text--small {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #FFFFFF;
  opacity: 0.7;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(4, 24, 39, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #E5E5E5;
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: #E5E5E5;
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  padding: 30px 0 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  z-index: 10;
  background-color: #041827;
  box-shadow: 0 0 15px 15px #041827;
}
@media (max-width: 320px) {
  .header {
    min-height: 70px;
  }
}
@media (max-width: 1023px) {
  .header {
    padding: 20px 0 10px;
    min-height: 70px;
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  gap: 10px;
  flex-wrap: wrap;
  color: #E5E5E5;
}

.header__logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__info {
  display: grid;
  grid-template-columns: repeat(2, 225px);
  column-gap: 30px;
  row-gap: 15px;
  grid-auto-flow: dense;
  align-items: flex-start;
}
@media (max-width: 1023px) {
  .header__info {
    padding: 10px;
    display: flex;
  }
}

.header__info-link {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #F2F5F6;
  font-family: "PT Mono";
  line-height: 18px;
}
@media (max-width: 1023px) {
  .header__info-link {
    padding: 10px;
  }
}

.header__info-link svg {
  fill: #00B2FF;
}

@media (max-width: 1023px) {
  .header__info-link span {
    display: none;
  }
}

.header__button {
  padding: 10px 16px;
  max-width: 160px;
  font-size: 14px;
  line-height: 16px;
  border: 1px solid #F2F5F6;
  border-radius: 3px;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

